
*{
    padding: 0;
    margin: 0;
    
}

li{
    list-style-type: none;
}
.nav{
  height: 60px;
  border-bottom: 1px solid #ccc;
  box-shadow: 0px 0px 5px #ccc;
  margin-bottom: 20px;
  box-sizing: border-box;
}
.imgDiv{
  width: 100%;
  height: 100%;
  overflow-wrap: break-word;
}
img{
  width: 40px;
  height: 40px;
  border: none;
  margin: 10px 0px;
  
}

.UnionTitle{
  width: 50%;
  height: 40px;
  margin: 10px auto;
  float: right;
}

.xsMoreBtn{
  height: 40px;
  margin: 10px 0px;
}

.search{
  height: 30px;
  width: 100%;
  margin: 15px 0;
}

.nav-text{
    color: black;
  }
  
  .leftNav{
    position: fixed;
    height: 100%;
    border-top: none;
  z-index: 9;

  }
* {
    padding: 0px;
    margin: 0px;
    list-style-type: none;
}

.menu {
    width: 300px;
    height: 300px;
    /* border: 1px solid #000; */
    box-sizing: border-box;
    margin: 50px auto;
    cursor: pointer;
}

.menu ul {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: 1px solid darkcyan;
    position: relative;
}

.menu ul li {
    position: absolute;
    left: 125px; /* 119 */
    top: 125px;
    width: 50px;
    height: 50px;
    background: darkcyan;
    border-radius: 5px;
    text-align: center;
    transition: all 0.5s ease 0.1s;
}

a {
    text-decoration: none;
    color: white;
    width: 100%;
    height: 100%;
    line-height: 50px;
    font-size: 12px;
    word-wrap:break-word; 
}
.personInfo{
    transition: all 0.5s ease 0.1s;
}
.personInfo:hover{
    box-shadow: 0px 0px 8px #ccc;
}
